import React, { useEffect, useState } from 'react';
import Preloader from '../Preloader/Preloader';
import ryptocurrency from '../ryptocurrency/ryptocurrency';

function urrencies({preloader, cruptos, error}) {

  return (
    <div className="urrencies">
      <h2 className="currencies__header"> </h2>
      { error.errorCoins ? <p className="currencies__error">    ,   .</p> : ''}
      { preloader ? <Preloader/> : 
        <div className="currencies__cryptocurrency-container">
          {cruptos.map((crypto) => (
            <ryptocurrency
              key={crypto.id}
              slug={crypto.slug}
              symbol={crypto.symbol}
              price={crypto.metrics.market_data.price_usd}
            />
          ))}
        </div>
      }
    </div>
  );
}

export default urrencies;
